import React, { useState, useRef, useEffect } from "react";
import "./ChatBot.css";

function ChatBot() {
  const [input, setInput] = useState("");
  const [messages, setMessages] = useState([]);
  const [questions, setQuestions] = useState([]);
  const [chatLoading, setChatLoading] = useState(false);
  const [genLoading, setGenLoading] = useState(false);
  const messagesEndRef = useRef(null);
  const [questionType, setQuestionType] = useState("mcq");
  const [count, setCount] = useState(5);

  // Scroll t ng xung cui
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages, questions]);

  // Gi tin nhn chat
  const sendMessage = async () => {
    if (!input.trim() || chatLoading) return;

    const userMessage = { id: Date.now(), role: "user", content: input.trim() };
    setMessages(prev => [...prev, userMessage]);
    setInput("");
    setChatLoading(true);

    try {
      const res = await fetch("https://on-tap-tin-hoc-ai.onrender.com/chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          message: userMessage.content,
          history: [...messages, userMessage].map(m => ({ role: m.role, content: m.content }))
        }),
      });

      const data = await res.json();
      const aiMessage = { id: Date.now() + 1, role: "assistant", content: data.reply || "AI cha tr li" };
      setMessages(prev => [...prev, aiMessage]);
    } catch (err) {
      console.error(err);
      setMessages(prev => [...prev, { id: Date.now() + 2, role: "assistant", content: "C li, th li sau!" }]);
    } finally {
      setChatLoading(false);
    }
  };

  // Nhn Enter  gi
  const handleKeyDown = (e) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  // To cu hi MCQ + True/False
  const generateQuestions = async () => {
  setQuestions([]);
  setGenLoading(true);
  try {
    const res = await fetch("https://on-tap-tin-hoc-ai.onrender.com/generate-questions", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        type: questionType,   // mcq | tf
        topic: input || "Tin hc THPT",
        count: count,
      }),
    });

    if (!res.ok) throw new Error("HTTP " + res.status);

    const data = await res.json();
    setQuestions(data);
  } catch (err) {
    console.error(err);
    alert("Li khi to cu hi!");
  } finally {
    setGenLoading(false);
  }
};



  return (
    <div className="chatbot-wrapper">
      <h3> Tr ging Tin hc</h3>

      <div className="chatbot-box">
        {/* Chat messages */}
        <div className="chatbot-messages">
          {messages.map(m => (
            <div key={m.id} className={m.role === "user" ? "msg user" : "msg ai"}>
              <b>{m.role === "user" ? "Bn:" : "AI:"}</b> {m.content}
            </div>
          ))}
          {chatLoading && <div className="msg ai">AI ang tr li...</div>}
          <div ref={messagesEndRef} />
        </div>

        {/* Chat input + button */}
        <div className="chatbot-input">
          <input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder="Hi tr ging Tin hc..."
          />
          <button onClick={sendMessage} disabled={chatLoading}>{chatLoading ? "ang gi..." : "Gi"}</button>
        </div>

        <div style={{ marginTop: 10, display: "flex", gap: 10, justifyContent: "center" }}>
          <select value={questionType} onChange={(e) => setQuestionType(e.target.value)}>
            <option value="mcq">Trc nghim</option>
            <option value="tf">ng / Sai</option>
          </select>

          <input
            type="number"
            min="1"
            max="20"
            value={count}
            onChange={(e) => setCount(Number(e.target.value))}
            style={{ width: 60 }}
          />
        </div>

        {/* Button to cu hi */}
        <div style={{ marginTop: "10px", textAlign: "center" }}>
          <button onClick={generateQuestions} disabled={genLoading || chatLoading}>
            {genLoading ? "ang to..." : "To cu hi cho HS"}
          </button>
        </div>

        {/* Hin th cu hi AI to */}
        {questions.map((q, idx) => (
          <div key={idx} className="question-box">
            <p><b>Cu {idx + 1}:</b> {q.question}</p>

            {q.type === "mcq" && q.options && (
              <ul>
                {q.options.map((opt, i) => (
                  <li key={i}>{opt}</li>
                ))}
              </ul>
            )}

            {q.type === "tf" && (
              <p>p n: {q.answer}</p>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export default ChatBot;
